// src/Tutorial.jsx
import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import './Tutorial.css';

const tutorialSteps = [
  { icon: '', title: '', description: ' 4  6  .' },
  { icon: '', title: '', description: '10   .' },
  { icon: '', title: '+', description: '4 , 6  .' },
  { icon: '', title: ' ', description: '10   .' }
];

export default function Tutorial() {
  const [step, setStep] = useState(-1);
  const navigate = useNavigate();

  const handleNext = () => {
    if (step >= tutorialSteps.length - 1) {
      navigate('/scan');
    } else {
      setStep(step + 1);
    }
  };

  const handleSkip = () => {
    navigate('/scan');
  };

  const current = step !== -1 ? tutorialSteps[step] : null;

  return (
    <div className={`tutorial-container ${step === -1 ? 'tutorial-intro' : ''}`}>
      <header className="tutorial-header">LabelTalk</header>
      <div className="tutorial-content">
        {step === -1 ? (
          <>
            <h1>LabelTalk</h1>
            <p>     .</p>
            <div className="tutorial-buttons">
              <button onClick={handleSkip}></button>
              <button onClick={handleNext}></button>
            </div>
          </>
        ) : (
          current && (
            <>
              <div className="tutorial-icon">{current.icon}</div>
              <h2>{current.title}</h2>
              <p>{current.description}</p>
              <div className="tutorial-buttons">
                <button onClick={handleSkip}></button>
                <button onClick={handleNext}></button>
              </div>
            </>
          )
        )}
      </div>
    </div>
  );
}
